.homeContainer {
  width: 100%;
}

.homeTitle {
  font-size: 32px;
  font-weight: 700;
  color: #07173d;
  margin: 0px 0px 20px 0px;
  text-align: center;
}
.homeTextMath {
  display: inline-flex;
  flex-wrap: wrap;
}

.homeTextMath div {
  width: min-content;
}

.homeImageContainer {
  width: 100%;
  text-align: center;
  margin-bottom: 12px;
}
.infoElementContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 14px;
  margin-top: 14px;
  .infoElement {
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 12px;
    border: 1px solid #0eb4ef;
    .infoElementTitle {
      font-size: 18px;
      font-weight: 600;
      color: #0f172a;
      margin-bottom: 8px;
    }
    .infoElementText {
      font-size: 16px;
      font-weight: 400;
      color: #0f172a;

      margin: 0;
    }
  }
}

.homeContainerBox {
  margin-top: 48px;
}
